* {
   font-family: 'Quicksand', sans-serif;
}

.main {
   margin-bottom: 15px;
}

/* Top bar */
.topbar {
   padding-top: 16px;
   padding-bottom: 10px;
   font-size: 2.2em;
   text-align: center;
   font-family: 'Lato';
}

.topbar a {
   color: #000;
}

.topbar a:hover {
   text-decoration: none;
}

/* Header */
.jumbotron:hover {
   -webkit-box-shadow: none;
           box-shadow: none;
}

.jumbotron {
   background-image: url('images/rocket.jpg');
   background-repeat: no-repeat;
   background-attachment: fixed;
   background-position: center;
   padding: 5rem 2rem;
   color: #fff;
   -webkit-transition: -webkit-box-shadow .5s ease;
   transition: -webkit-box-shadow .5s ease;
   -o-transition: box-shadow .5s ease;
   transition: box-shadow .5s ease;
   transition: box-shadow .5s ease, -webkit-box-shadow .5s ease;
   /*box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);*/
}

.jumbotron-content {
   padding-top: 10px;
}

.avatar {
   -webkit-box-shadow: 0px 0px 5px #fff;
           box-shadow: 0px 0px 5px #fff;
}

.short-bio {
   text-align: left;
   margin: 0;
   padding-top: 16px;
   font-size: 19px;
   font-weight: 300;
}

/* Navigation */
.nav-pills {
   margin-bottom: 35px;
}

.nav-pills .nav-link {
   color: #111;
   outline: none;
   border: 1px solid rgba(64, 64, 64, 0.17);
   -webkit-transition: all .5s ease;
   -o-transition: all .5s ease;
   transition: all .5s ease;
   margin-right: 6px;
   -webkit-box-shadow: 0 2px 3px rgba(0, 0, 0, 0), 0 1px 2px rgba(0,0,0,0.23);
           box-shadow: 0 2px 3px rgba(0, 0, 0, 0), 0 1px 2px rgba(0,0,0,0.23);
}

.nav-pills .nav-link:hover {
   cursor: pointer;
   -webkit-box-shadow: none;
           box-shadow: none;
}

.nav-pills .nav-link.active, .nav-pills .show>.nav-link {
   color: white!important;
}

.nav-pills .nav-link .active:hover {
   color: white!important;
}

.tab-content {
   margin-top: 25px;
}

/* About */
.about-bio {
   text-align: justify;
}

/* Experience */
.card-container {
   height: auto;
   border-radius: 7px;
   border-left: 4px solid #79eab6;
   border-top: 1px solid rgba(64, 64, 64, 0.15);
   border-right: 1px solid rgba(64, 64, 64, 0.15);
   border-bottom: 1px solid rgba(64, 64, 64, 0.15);
   margin-bottom: 15px;
   padding: 10px 20px;
   -webkit-transition: all .5s ease;
   -o-transition: all .5s ease;
   transition: all .5s ease;
   /*height: auto;
   border-radius: 7px;
   background: rgba(245, 245, 245, 0.58);*/
   /* border: 1px solid rgba(41, 12, 12, 0.17); */
   /*border-left: 4px solid #29D28B;
   margin-bottom: 15px;
   padding: 10px 20px;*/
   /* line-height: 15px; */
   /*transition: all .5s ease;
   box-shadow: 0 2px 3px rgba(0, 0, 0, 0), 0 1px 2px rgba(0,0,0,0.23);*/
}

.card-container p {
   margin-bottom: 5px;
}

.card-container .company-name {
   font-size: 23px;
}

.card-container:hover {
   -webkit-box-shadow: 0 0 15px #ddd;
           box-shadow: 0 0 15px #ddd;
}

.badge-success {
   background: #29D28B;
}

.edu {
   border-left: 4px solid #00B7FF;
}

.win {
   /* font-size: 2px; */
   color: #f5f5f5;
}

/* Skills */
.lang-container {
   padding: 20px;
   text-align: center;
   width: 150px;
   /*background: #eaeaea;*/
   border-radius: 10px;
   margin-right: 15px;
   margin-bottom: 15px;
   float: left;
   -webkit-box-shadow: none;
           box-shadow: none;
   -webkit-transition: all ease .4s;
   -o-transition: all ease .4s;
   transition: all ease .4s;
   border: 1px solid rgba(64, 64, 64, 0.15);
}

.lang-container:hover {
   color: #4971e8;
   -webkit-box-shadow: 0 0 8px #ddd;
           box-shadow: 0 0 8px #ddd;
}

.lang-ico {
   font-size: 35px;
}

.lang-title {
   margin-top: 10px;
}

.backend, .backend-title {
   margin-top: 15px;
}

.icon-osx, .icon-java-bold, .icon-python, .icon-nodejs, .icon-javascript {
   position: relative;
   left: 5px;
}

/* Projects */
.project-card {
   height: auto;
   border-radius: 7px;
   border-left: 4px solid #79b1ea;
   border-top: 1px solid rgba(64, 64, 64, 0.15);
   border-right: 1px solid rgba(64, 64, 64, 0.15);
   border-bottom: 1px solid rgba(64, 64, 64, 0.15);
   margin-bottom: 15px;
   padding: 10px 20px;
   -webkit-transition: all .5s ease;
   -o-transition: all .5s ease;
   transition: all .5s ease;
}

.project-card:hover {
   -webkit-box-shadow: 0 0 15px #ddd;
           box-shadow: 0 0 15px #ddd;
}

.project-name {
   font-size: 25px;
}

.personal-projects-title, .personal-projects-container {
   margin-top: 15px;
}

.contact-link:hover {
   text-decoration: none;
}

/* Custom, iPhone Retina */
@media only screen and (max-width : 320px) {
   body{font-size: 10px;}
   .lang-container {
      padding: 10px;
      width: 128px;
      margin-left: 11px;
   }
}

 /* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {
   body{font-size: 14px;}
   a {
      display: block;
      overflow: hidden;
      white-space: nowrap;
      -o-text-overflow: ellipsis;
         text-overflow: ellipsis;
   }
   .short-bio {
      font-size: 15px;
      text-align: justify;
   }
   .jumbotron {
      padding-top: 20px;
      padding-bottom: 20px;
      -webkit-box-shadow: none;
              box-shadow: none;
   }
   .avatar-container {
      text-align: center;
   }
   .avatar {
      width: 40%!important;
   }
   .nav-link {
      width: 100%;
      margin-right: 0px!important;
      /*border-radius: 0px!important;*/
      border: none!important;
      -webkit-box-shadow: none!important;
              box-shadow: none!important;
   }
   .about-title, .backend-title {
      margin-bottom: 15px;
      text-align: center;
   }
   .lang-container {
      margin-right: 0px!important;
      margin-left: 9px;
   }
}



